<template>
    <div class="rise x add question">
        <el-container v-loading="loading">
            <div class="titleHeader">
                <div class="title">{{header}}</div>            
                <div class="right">
                    <div class="item" v-for="(item, index) in headerBtn" :key="index">
                        <button type="button" class="el-button btn el-button--primary" @click="headerClick(index)"><span>{{item}}</span></button>
                    </div>
                </div> 
            </div>
            <div class="header">
                <div class="left">
                    <div class="item">
                        <span>问卷名称：</span>
                        <el-input v-model="title" placeholder="请输入问卷名称"/>
                    </div>
                    <div class="item">
                        <span>开始时间：</span>
                        <el-date-picker v-model= "beginTime" placeholder="请输入开始时间" type="date"/>
                    </div>
                    <div class="item">
                        <span>结束时间：</span>
                        <el-date-picker v-model= "endTime" placeholder="请输入结束时间" type="date"/>
                    </div>
                </div>         
            </div>
            <div class="content" v-if="type != 'new'">
                <div class="line"></div>               
                <div class="article">
                    <div class="top">
                        <div class="item">
                            <span>题目类型：</span>
                            <el-select @change="selectChange" v-model="product.type" placeholder="请选择题目类型">
                                <el-option
                                    v-for="item in topicList"
                                    :key="item.id"
                                    :label="item.dValue"
                                    :value="item.id"
                                ></el-option>
                            </el-select>
                        </div>
                        <div class="item">
                            <span>题目名称：</span>
                            <el-input v-model="product.name" placeholder="请输入题目名称"/>
                        </div>
                    </div>                        
                    <div class="item" v-if="product.type == 0">
                        <span>选项：</span>
                        <el-input v-model="productOpt.content" placeholder="请输入选项内容"/>
                        <div class="item-select">
                            <div class="item-opt" v-for="(item, index) in product.options" :key="index">
                                <span>{{item.content}}</span>
                                <span v-if="item.content" class="red" @click="themeOptDel(item, index)">删除</span>
                            </div>
                        </div>
                    </div>
                    <div class="item">
                        <span></span>
                        <button type="button" class="add el-button btn el-button--primary" @click="themeClick()"><span>出题</span></button>                        
                        <button v-if="product.type == 0" type="button" class="add el-button btn el-button--primary" @click="themeOptAdd()"><span>选项添加</span></button>                            
                        <!-- <button type="button" class="add el-button btn el-button--primary" @click="themeClear()"><span>清空</span></button> -->
                    </div>
                </div>
                <div class="line" v-if="data.length > 0"></div>
                <div class="cont">
                    <div class="item" v-for="(item, index) in data" :key="index">
                        <div class="item-name">
                            <span>{{index + 1}}、{{item.title}}</span>
                            <span class="red" @click="themeDel(item, index)">试题删除</span>
                        </div>
                        <div class="item-opt">
                            <div v-if="item.item1">{{item.item1}}</div>
                            <div v-if="item.item2">{{item.item2}}</div>
                            <div v-if="item.item3">{{item.item3}}</div>
                            <div v-if="item.item4">{{item.item4}}</div>
                            <div v-if="item.item5">{{item.item5}}</div>
                        </div>
                    </div>
                </div>
            </div>
        </el-container>
    </div>
</template>

<script>
import queryString from "querystring";
import { DateDay } from 'utils/formData';
import { topicList, topicOption } from 'data/listBody';
const headers = ["新建", "编辑"];
const headerBtn = ["取消","保存"];
const tips = ['请补全选项内容','请补全题目内容','请勿重复添加']

export default {
    name: "add",
    data() {
        return {
            title: "",
            id: "",
            type: "",
            beginTime: "",
            endTime: "",
            header: "",
            headerBtn: headerBtn,
            topicList: topicList,
            topicOption: topicOption,
            typeOption:'',
            loading: false,
            data: [], //问卷
            name: '', //问卷的名称
            product: { //当前题目
                type: '',
                name: '',               
                options: []
            },
            productOpt:{ //当前选项
                type:'',
                content: ''
            }
        };
    },
    mounted() {
        this.dataInit();
        // this.dropInit();
    },
    methods: {
        dataInit() {
            const { type, id } = this.$route.query;
            this.type = type;
            this.id = id;            
            if (type !== "edit") return (this.header = headers[0]);
            this.header = headers[1];
            this.detailAjax();
            this.ajax();
        },
        headerClick(item){
            if(item == 0) this.$router.go(-1);
            if(item == 1) this.save();
        },
        editInit(res) {
            const { title, beginTime, endTime } = res.data;
            this.title = title;
            this.beginTime = beginTime;
            this.endTime = endTime;
        },  
        detailAjax() {
            //查看详情
            this.loading = true;
            this.api.get(`/survey/${this.id}`).then(res => {
                this.loading = false;
                this.editInit(res);
            })
            .catch(err => {
                console.log(err);
                this.loading = false;
                this.$message(err.message);
            });
        },   
        save() { //保存详情
            const params = {
                title: this.title,
                beginTime: DateDay(this.beginTime),
                endTime: DateDay(this.endTime),
            };
            const axiosType = ["post", "put"];
            let type = axiosType[0];
            if (this.type === "edit") {
                params.id = this.id;
                type = axiosType[1];
            }
            this.loading = true;
            this.api[type](`/survey`, params, "form").then(res => {
                this.loading = false;
                this.$router.go(-1);
            })
            .catch(err => {
                this.loading = false;
                console.log(err);
                this.$message(err.message);
            });
        },
        themeOptAdd(){ //选项添加
            let opt = Object.assign({}, this.productOpt);
            let options = [].concat(this.product.options);
            let filter = options.filter(item => {
                return item.content === opt.content
            })
            if(!opt.content) return this.$message(tips[0]);
            if(filter.length > 0) return this.$message(tips[2])
            options.push(opt);
            this.product.options = options;
        },
        themeOptDel(obj, index){ //选项删除
            let options = [].concat(this.product.options);
            let filter = options.filter(item => {
                return obj.content !== item.content
            })
            this.product.options = filter;
        },
        themeClear(){ //试题清空
            this.product = {
                type: '',
                name: '',
                options: []
            }
            this.productOpt = {
                type: '',
                content: '',
            }
        },
        themeClick(){ //试题出题
            let opt = Object.assign({}, this.product);
            if(!opt.name) return this.$message(tips[0])
            if(!opt.type && opt.type != 0) return this.$message(tips[0])
            for(var i = 0; i < 5; i++){
                if(!opt.options[i]) opt.options[i] = {}
            }
            const params = {
                title: opt.name,
                type: opt.type,
                content: '',
                surveyId: this.id,
                item1: opt.options[0].content,
                item2: opt.options[1].content,
                item3: opt.options[2].content,
                item4: opt.options[3].content,
                item5: opt.options[4].content,
            }
            this.loading = true;
            this.api.post(`/question`, params, "form").then(res => {
                this.themeClear();
                this.ajax();
            })
            .catch(err => {
                console.log(err);
                this.loading = false;
                this.$message(err.message);
            });
        },
        themeDel(obj, index){ //试题删除
            this.loading = true;
            this.api.delete(`/question/${obj.id}`).then(res => {
                this.$message(res.data);                
                this.ajax();
            })
            .catch(err => {
                console.log(err);
                this.loading = false;
                this.$message(err.message);
            });
        },
        selectChange(e){ //题目类型切换
            this.themeClear()
            this.product.type = e;
        },
        ajax() {
            //查看所有问题
            this.loading = true;
            this.api.get(`/question?surveyId=${this.id}`).then((res)=>{
                this.loading = false;
                this.data = res.data;
            }).catch((err) => {
                console.log(err)
                this.loading = false;
                this.$message(err.message)
            })
        },
    }
};
</script>
 
<style lang="less" scoped>
.question.add{
    text-align: left;
    padding: 0 20px !important;
    .titleHeader{
        display: flex;
        align-items: center;
        justify-content: space-between;
        .title{
            font-size: inherit;
            margin-bottom: 0;
            font-size: 20px;
            margin-top: 20px;
        }
        .right{
            display: flex;
            .item{
                margin-left: 20px;
            }
        }
    }    
    .el-container{
        display: block;
        height: 100%;
    }
    .header{
        .left{
            display: flex;
            justify-content: flex-start;
            flex-wrap: wrap;
        }
    }
    .content{
        .line{
            height: 2px;
            width: 100%;
            background: #eee;
            margin: 1% 0;
        }
        .article{
            .top{
                display: flex;
                flex-wrap: wrap;
                .item{
                    display: flex;
                    flex-direction: inherit;
                    align-items: center;
                }
            }
            .item{
                .el-select{
                    margin-right: 0;
                }
                .el-input{
                    margin-right: 20px;
                }
                .item-select{
                    display: flex;
                    flex-wrap: wrap;
                    .item-opt{
                        margin-right: 20px;
                    }
                }                
            }  
        }
        .cont{
            padding-left: 40px;
            .item{
                flex-direction: column;
                align-items: flex-start;
                padding: 5px 0;
                .item-opt{
                    display: flex;
                    margin-top: 10px;
                    div{
                        margin-right: 20px;
                        margin-left: 15px;
                        position: relative;
                        font-size: 14px;
                        &::before{
                            content: '';
                            position: absolute;
                            width: 10px;
                            height: 10px;
                            left: -15px;
                            top: 2px;
                            border:1px solid rgba(#333,.3);
                        }
                    }
                }
                .item-name{
                    &:hover{
                        .red{
                            display: inline-block;
                        }
                    }
                }
                .red{
                    margin-left: 20px;
                    display: none;
                }
            }
        }
    }
    .red{
        color: red;
        font-size: 12px;
        margin-left: 10px;
        cursor: pointer;
    }
    .el-select{
        margin-right: 20px;
    }
}
</style>
 